import { ComponentProps } from "react"
import { MAX_WIDTH_CONTAINER_PADDING_X_OFFSET } from "@rp/common/settings"
import { Container, styled } from "@rp/react/web/mui/core"

type MaxWidthContainerProps = ComponentProps<typeof Container> & {
  isPane?: boolean
}

const MaxWidthContainer = styled(Container, {
  name: "ContentContainer",
  shouldForwardProp: propName => propName !== "isPane",
})<MaxWidthContainerProps>(({ theme, isPane = false }) => ({
  maxWidth: "100%",
  flexGrow: 1,
  overflow: isPane ? "visible" : "hidden",
  [theme.breakpoints.up("md")]: {
    paddingLeft: isPane ? 0 : MAX_WIDTH_CONTAINER_PADDING_X_OFFSET,
  },
  [theme.breakpoints.up("lg")]: {
    maxWidth: isPane ? undefined : "100%",
  },
  [theme.breakpoints.up("xl")]: {
    maxWidth: isPane ? undefined : theme.breakpoints.values.xl,
  },
}))

export { MaxWidthContainer }
